import React, { Component } from 'react'
import releases from '../../lib/releases.js'

export default class NodeRelease extends Component {
  constructor() {
    super()
    this.state = { }
  }

  componentDidMount() {
    releases().then((a) => {
      this.setState(a)
    });
  }

  render() {
    return (
      <div>
        <span style={{
          fontSize: '2em'
        }}>
          Average of {this.state.averageDay} days
        </span>

        <p>between minor releases since April</p>
      </div>
    )
  }
}



